body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  text-align: center;
}

.quiz-options button {
  margin: 10px;
}

.quiz-start {
  margin: 20px;
}

.timer {
  font-size: 1.2em;
  margin-top: 10px;
  color: #ff0000;
}

.card-inner {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  backface-visibility: hidden;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.card-front, .card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  backface-visibility: hidden;
}

.card-back {
  transform: rotateY(180deg);
}

@media (max-width: 600px) {
  .App-header {
    padding: 10px;
  }

  .App-header h1 {
    font-size: 1.8em;
  }

  .App-header button {
    margin: 5px;
    padding: 8px 16px;
    font-size: 0.9em;
  }

  nav a {
    font-size: 1em;
  }

  .quiz-options button {
    padding: 8px;
  }

  .quiz-results h2, .quiz-results p {
    font-size: 1.2em;
  }

  .answers-summary h3 {
    font-size: 1.2em;
  }
}
